import { useDispatch } from 'react-redux';
import { changeOptions } from '../../pages/home/slice'
import {
    Form,
    Checkbox,
    Input,
    Radio
} from 'antd';

const App = (data) => {
    let options = { ...data.options }

    const dispatch = useDispatch()

    return (
        <Form layout="vertical">
            <Form.Item label="">
                <Checkbox onChange={(e) => {
                    options.isText = e.target.checked;
                    dispatch(changeOptions(options))
                }} ></Checkbox>
            </Form.Item>

            {
                options.isText ? (<Form.Item label="">
                    <Input onChange={(e) => {
                        options.text = e.target.value;
                        dispatch(changeOptions(options))
                    }} placeholder="" />
                </Form.Item>) : ''
            }

            <Form.Item label="">
                <Radio.Group defaultValue='default' onChange={(e) => {
                    options.size = e.target.value
                    dispatch(changeOptions(options))
                }}>
                    <Radio value='default'></Radio>
                    <Radio value='small'></Radio>
                </Radio.Group>
            </Form.Item>

            <Form.Item>
                <Checkbox onChange={(e) => {
                    options.autoFocus = e.target.checked
                    dispatch(changeOptions(options))
                }}></Checkbox>
            </Form.Item>

            <Form.Item>
                <Checkbox onChange={(e) => {
                    options.defaultChecked = e.target.checked
                    dispatch(changeOptions(options))
                }}></Checkbox>
            </Form.Item>

            <Form.Item>
                <Checkbox onChange={(e) => {
                    options.disabled = e.target.checked
                    dispatch(changeOptions(options))
                }}></Checkbox>
            </Form.Item>

            <Form.Item>
                <Checkbox onChange={(e) => {
                    options.loading = e.target.checked
                    dispatch(changeOptions(options))
                }}></Checkbox>
            </Form.Item>
        </Form>
    );
};

export default App;