import React from 'react';
import { Form, Checkbox, DatePicker, Upload, Radio } from 'antd';
import { LoadingOutlined, PlusOutlined } from '@ant-design/icons';

function clockIn() {
  let loading = false;

  const uploadButton = (
    <div>
      {loading ? <LoadingOutlined /> : <PlusOutlined />}
      <div
        style={{
          marginTop: 8,
        }}
      >
        Upload
      </div>
    </div>
  );

  return (
    <>
      <Form
        labelCol={{ span: 4 }}
        wrapperCol={{ span: 14 }}
        layout="horizontal"
      >
        <Form.Item label="" valuePropName="checked">
          <Checkbox.Group
            options={['', '', '', '', '', '']}
            defaultValue={['Apple']} />
        </Form.Item>

        <Form.Item label="">
          <Radio.Group>
            <Radio value="inSchool"> </Radio>
            <Radio value="leaveSchool">  </Radio>
          </Radio.Group>
        </Form.Item>

        <Form.Item label="">
          <Radio.Group>
            <Radio value="noIsolation"> </Radio>
            <Radio value="inSchool">  </Radio>
            <Radio value="inHome">  </Radio>
            <Radio value="all">  </Radio>
          </Radio.Group>
        </Form.Item>

        <Form.Item label="">
          <DatePicker />
        </Form.Item>

        <Form.Item label="">
          <Radio.Group>
            <Radio value="yin"> </Radio>
            <Radio value="yang"> </Radio>
          </Radio.Group>
        </Form.Item>

        <Form.Item label="">
          <Upload
            name="avatar"
            listType="picture-card"
            className="avatar-uploader"
            showUploadList={false}
            action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
          >
              {uploadButton}
          </Upload>
        </Form.Item>
      </Form>
    </>
  );
}

export default clockIn;