import { useDispatch } from 'react-redux';
import { changeOptions } from '../../pages/home/slice'
import {
    Form,
    Select,
    Input,
    Button,
} from 'antd';
import { FontColorsOutlined} from '@ant-design/icons';
import '../../pages/home/console/consoleStyle.css'
const { Option } = Select;

const App = (data) => {
    let options = { ...data.options };
    const ButtonGroup = Button.Group;
    const dispatch = useDispatch()

    return (
        <div className='textConsole'>
        <h2  className='title'>
        <FontColorsOutlined /></h2>

        <Form layout="vertical"
            className='formStyle'>
                
                <Form.Item label="" >
                    <Input onChange={(e) => {
                        options.value = e.target.value;
                        dispatch(changeOptions(options))
                    }} placeholder="" />
                </Form.Item>
                <Form.Item label="">
                    <ButtonGroup>
                        <Button
                            onClick={(e) => {
                                // 
                                options.justifyContent = 'start';
                                console.log(options);
                                dispatch(changeOptions(options))
                            }}
                        ></Button>
                        <Button
                            onClick={(e) => {
                                // 
                                options.justifyContent = 'center';
                                console.log(options);
                                dispatch(changeOptions(options))
                            }}
                        ></Button>
                        <Button
                            onClick={(e) => {
                                // 
                                options.justifyContent = 'end';
                                console.log(options);
                                dispatch(changeOptions(options))
                            }}
                        ></Button>

                    </ButtonGroup>
                </Form.Item>
                <Form.Item label="">
                    <ButtonGroup>
                        <Button
                            onClick={(e) => {
                                options.fontSize = '8px';
                                console.log(options);
                                dispatch(changeOptions(options))
                            }}></Button>
                        <Button
                            onClick={(e) => {
                                options.fontSize = '18px';
                                console.log(options);
                                dispatch(changeOptions(options))
                            }}></Button>
                        <Button
                            onClick={(e) => {
                                options.fontSize = '24px';
                                console.log(options);
                                dispatch(changeOptions(options))
                            }} ></Button>
                    </ButtonGroup>
                </Form.Item>
                <Form.Item label="">
                    <ButtonGroup>
                        <Button
                            onClick={(e) => {
                                options.fontWeight = '400';
                                console.log(options);
                                dispatch(changeOptions(options))
                            }}></Button>
                        <Button
                            onClick={(e) => {
                                options.fontWeight = '700';
                                console.log(options);
                                dispatch(changeOptions(options))
                            }}
                        ></Button>
                    </ButtonGroup>
                </Form.Item>

                <Form.Item label="">
                    <Select defaultValue="Microsoft YaHei" 
                    style={{ width: 120 }} onChange={(e) => {
                        options.fontFamily = e;
                        dispatch(changeOptions(options))
                    }}>
                        <Option
                            value="kaiti"
                            style={{ fontFamily: "kaiti" }}

                        ></Option>
                        <Option
                            value="SimHei"
                            style={{ fontFamily: "SimHei" }}
                        ></Option>
                        <Option
                            value="SimSun"
                            style={{ fontFamily: "SimSun" }}
                        ></Option>
                        <Option
                            value="STFangsong"
                            style={{ fontFamily: "STFangsong" }}
                        ></Option>
                        <Option
                            value="Microsoft YaHei"
                            style={{ fontFamily: "Microsoft YaHei" }}
                        ></Option>
                    </Select>
                </Form.Item>

                <Form.Item label="">
                    <Select defaultValue="#000" style={{ width: 120 }} onChange={(e) => {
                        options.color = e;
                        dispatch(changeOptions(options))
                    }}>
                        <Option value="red" style={{ color: "red" }} ></Option>
                        <Option value="blue" style={{ color: "blue" }}></Option>
                        <Option value="green" style={{ color: "green" }}></Option>
                        <Option value="yellow" style={{ color: "yellow" }}></Option>
                        <Option value="purple" style={{ color: "purple" }}></Option>
                        <Option value="#000" style={{ color: "#000" }}></Option>
                        <Option value="#777" style={{ color: "#777" }}></Option>
                    </Select>
                </Form.Item>

            </Form >
        </div>

    );
};

export default App;