import {
    MailOutlined,
    PieChartOutlined,
} from '@ant-design/icons';
import Template from './components/template';
import Index from './components/index';
import Establish from '../establish/resume';
import { Menu } from 'antd';
import React, { useState } from 'react';
import './indexStyle.scss'
import { get, post } from '../../api/http'
// 
import { Routes, Route, Navigate, useNavigate } from "react-router-dom"

function getItem(label, key, icon, children, type) {
    return {
        key,
        icon,
        children,
        label,
        type,
    };
}

const items = [
    getItem('', 'index', <PieChartOutlined />),
    getItem('', 'sub1', <MailOutlined />, [
        getItem('', '/home'),
        getItem('', 'template'),
    ]),
];
function getFun() {
    // http://localhost:8080
    post('/api/mform/addMForm', {
        id: "123112",
        name: "test",
        rules: {
            username: "admin",
            password: "123456"
        },
        data: [
            {code: 1}
        ]
    }).then(res => {
        console.log(res);
    })
}

function findOne() {
    get('/api/mform/findOne', {
        id: "123112"
    }).then(res => {
        console.log(res);
    })
}

const App = () => {
    const [collapsed] = useState(false);
    const [current, setCurrent] = useState('index');
    const navigate = useNavigate()
    const onClick = (e) => {
        console.log('click ', e);
        setCurrent(e.key);
        navigate(e.key)
    };
    // getFun()
    return (
        <>
            <div
                className='leftMenu'
            >
                <h3 onClick={()=>findOne()}>visual-editor</h3>
                <Menu
                    defaultSelectedKeys={['index']}
                    defaultOpenKeys={['sub1']}
                    mode="inline"
                    theme="dark"
                    inlineCollapsed={collapsed}
                    items={items}
                    onClick={onClick}
                />
            </div>
            <div className='content'>
                <Routes>
                    <Route path="template" element={<Template />}></Route>
                    <Route path="/index" element={<Index />}></Route>
                    <Route path="*" element={<Navigate to="index" />} />
                    <Route path="establish" element={<Establish />} />
                </Routes>
            </div>

        </>
    );
};

export default App;