import { Button, Card, Empty  } from 'antd';
import { useNavigate } from 'react-router';
function Template() {
    const navigate = useNavigate();
    return (
        <div className="template">
            <div>
                <h2></h2>

                 <Card
                    title=""
                    extra={<a onClick={() => {navigate('/estab',{state:{name:'resume'}})}}></a>}
                    style={{
                        width: 300,
                        display: 'inline-block',
                        marginRight: 10
                    }}
                >
                    <p></p>
                    <p></p>
                    <p></p>
                </Card>

                <Card
                    title=""
                    extra={<a href="clockIn"></a>}
                    style={{
                        width: 300,
                        display: 'inline-block',
                        marginRight: 10
                    }}
                >
                    <p></p>
                    <p></p>
                </Card>
                
                <Card
                    title=""
                    extra={<a href="establish"></a>}
                    style={{
                        width: 300,
                        display: 'inline-block',
                        marginRight: 10
                    }}
                >
                    <p></p>
                    <p></p>
                    <p></p>
                </Card>
            </div>
            <div>
            <h2></h2>
            <Empty />
            </div>
        </div>
    )
}
export default Template