import { useDispatch } from 'react-redux';
import { changeOptions,changeId } from '../../pages/home/slice'
import {
  Form,
  Checkbox,
  Input,
  Button
} from 'antd';
import { FontColorsOutlined, } from '@ant-design/icons';

const count = 0
const App = (data) => {

  let options = { ...data.options }

  const dispatch = useDispatch();

  // text
  let copyText = [...options.inputValue];

  // input
  function addInput() {
    options.inputValue = [...options.inputValue, '' + (count++)];
    copyText= options.inputValue;
    dispatch(changeOptions(options));
  }

  return (
    <div className="radioConsole">
      <Form layout="vertical"
        className='formStyle'
      >
        <h2 className='title'>
          <FontColorsOutlined /> </h2>
        <Form.Item label="">
          <Input onChange={(e) => {
            options.title = e.target.value;
            dispatch(changeOptions(options));
          }}
            placeholder=""
          />
        </Form.Item>
        <Form.Item label="">
          <Input onChange={(e) => {
            options.tooltip = e.target.value;
            dispatch(changeOptions(options));
          }}
            placeholder=""
          />
        </Form.Item>

      <Form.Item label="">
        {
          copyText.map((item, index) => {
            return (
              <Input
                onChange={(e) => {
                  copyText[index] = e.target.value;
                  options.inputValue = [...copyText];
                  dispatch(changeOptions(options));
                }}
                placeholder=""
                key={index}
                style={{borderRadius: '5px'}}
              />
            )
          })
        }
        <Button onClick={addInput}></Button>
      </Form.Item>



      </Form>
      <Form.Item label=""
        style={{
          fontFamily: '',
        }}
      >
        <Checkbox
          onChange={(e) => {
            options.checked = e.target.checked; console.log(options);
            dispatch(changeOptions(options))
          }}
        >
          
        </Checkbox>
      </Form.Item>
      <Form.Item
        style={{
          fontFamily: '',
        }}
      >
        <Checkbox onChange={(e) => {
          options.autoFocus = e.target.checked
          dispatch(changeOptions(options))
        }}></Checkbox>
      </Form.Item>

      <Form.Item>
        <Checkbox onChange={(e) => {
          options.disabled = e.target.checked
          dispatch(changeOptions(options))
        }}></Checkbox>
      </Form.Item>
    </div>
  );
};

export default App;