import { useDispatch } from 'react-redux';
import { changeOptions } from '../../pages/home/slice'
import {
  Form,
  Select,
  Button,
  Input,
  Checkbox
} from 'antd';
import { FontColorsOutlined, MailOutlined, PhoneOutlined, SmileOutlined, PictureOutlined, PaperClipOutlined, WalletOutlined, AppstoreAddOutlined, ScheduleOutlined } from '@ant-design/icons';
const App = (data) => {
  const dispatch = useDispatch()
  const ButtonGroup = Button.Group;
  let options = { ...data.options }
  return (
    <div className="rateConsole">

      <h2 className='title'>
        <SmileOutlined /> </h2>

        <Form layout="vertical"
            className='formStyle'>
        {/* */}
        <Form.Item label="">
          <Input
            placeholder=""
            onChange={(e) => {
              options.title = e.target.value;
              console.log(options);
              dispatch(changeOptions(options))
            }} />

        </Form.Item>
        {/*  */}
        <Form.Item label="">
          <Input
            placeholder=""
            className='tips'
            onChange={(e) => {
              options.tips = e.target.value; console.log(options);
              dispatch(changeOptions(options))
            }} />
        </Form.Item>
        <Form.Item label="" >
          <ButtonGroup>
            <Button
              onClick={(e) => {
                // 
                options.justifyContent = 'start';
                console.log(options);
                dispatch(changeOptions(options))
              }}
            ></Button>
            <Button
              onClick={(e) => {
                // 
                options.justifyContent = 'center';
                console.log(options);
                dispatch(changeOptions(options))
              }}
            ></Button>
            <Button
              onClick={(e) => {
                // 
                options.justifyContent = 'end';
                console.log(options);
                dispatch(changeOptions(options))
              }}
            ></Button>

          </ButtonGroup>
        </Form.Item>
      </Form>
      <Form.Item label=""
        style={{
          fontFamily: '',
        }}
      >
        <Checkbox
          onChange={(e) => {
            options.checked = e.target.checked; console.log(options);
            dispatch(changeOptions(options))
          }}
        >
          
        </Checkbox>
      </Form.Item>
    </div>
  );
};
export default App;