import { useDispatch } from 'react-redux';
import { changeOptions } from '../../pages/home/slice'
import {
    Form,
    Input,
    Select,
    Checkbox,
    Radio
} from 'antd';
import {AppstoreAddOutlined} from '@ant-design/icons';
const { Option } = Select;


const App = (data) => {
    let options = { ...data.options }
    const RadioGroup = Radio.Group;
    const RadioButton = Radio.Button;
    const dispatch = useDispatch()

    return (
        <div className="buttonConsole">
            <h2 className='title'
            ><AppstoreAddOutlined/> </h2>

            <Form layout="vertical"
                style={{
                    fontWeight: 700,
                    fontFamily: '',
                }}
            >
                <Form.Item label="">
                    <Input
                        onChange={(e) => {
                            options.value = e.target.value;
                            dispatch(changeOptions(options))
                        }} placeholder="" />
                </Form.Item>
               
                <Form.Item label="">
                    <Select defaultValue="primary" style={{ width: 120 }} onChange={(e) => {
                        options.style = e;
                        dispatch(changeOptions(options))
                    }}>
                        <Option value="default"></Option>
                        <Option value="primary"></Option>
                        <Option value="ghost"></Option>
                        <Option value="dashed"></Option>
                        <Option value="link"></Option>
                        <Option value="danger"></Option>
                    </Select>
                </Form.Item>

                <Form.Item label="">
                    <Select defaultValue="default" style={{ width: 120 }} onChange={(e) => {
                        options.shape = e;
                        dispatch(changeOptions(options))
                    }}>
                        <Option value="default"></Option>
                        <Option value="round"></Option>
                    </Select>
                </Form.Item>

                <Form.Item label="">
                    <Checkbox onChange={(e) => {
                        options.disabled = e.target.checked;
                        dispatch(changeOptions(options))
                    }}></Checkbox>
                </Form.Item>
                <Form.Item label="">
                    <RadioGroup
                        defaultValue="a"
                        buttonStyle="solid"
                    >
                        <RadioButton
                            value="a"
                            onClick={(e) => {
                                options.width = '25%';
                                console.log(options);
                                dispatch(changeOptions(options))
                            }} >25</RadioButton>
                        <RadioButton
                            value="b"
                            onClick={(e) => {
                                options.width = '50%';
                                console.log(options);
                                dispatch(changeOptions(options))
                            }} >50</RadioButton>
                        <RadioButton
                            value="c"
                            onClick={(e) => {
                                options.width = '75%';
                                console.log(options);
                                dispatch(changeOptions(options))
                            }}  >75</RadioButton>
                        <RadioButton
                            value="d"
                            onClick={(e) => {
                                options.width = '100%';
                                console.log(options);
                                dispatch(changeOptions(options))
                            }}  >100</RadioButton>
                    </RadioGroup>
                </Form.Item>
                <Form.Item label="">
                    <Input onChange={(e) => {
                        options.href = e.target.value;
                        dispatch(changeOptions(options))
                    }} placeholder="" />
                </Form.Item>
                <Form.Item label=""
                // style={{ display: options.href ? 'block' : 'none' }}
                >
                    <Checkbox onChange={(e) => {
                        options.target = e.target.checked ? '_blank' : '_self';
                        dispatch(changeOptions(options))
                    }}></Checkbox>
                </Form.Item>
            </Form>
        </div>
    );
};

export default App;