import { useDispatch } from 'react-redux';
import { changeOptions } from '../../pages/home/slice'
import {
  Form,
  Checkbox,
  Input,
  Button
} from 'antd';

// 
let count = 0;

const App = (data) => {

  let options = { ...data.options }

  const dispatch = useDispatch();

  // text
  let copyText = [...options.inputValue];

  // input
  function addInput() {
    options.inputValue = [...options.inputValue, '' + (count++)];
    copyText= options.inputValue;
    dispatch(changeOptions(options));
  }

  return (
    <Form layout="vertical">

      <Form.Item label="">
        <Input onChange={(e) => {
          options.title = e.target.value;
          dispatch(changeOptions(options));
        }} 
        placeholder=""
        />
      </Form.Item>

      <Form.Item label="">
        <Input onChange={(e) => {
          options.tooltip = e.target.value;
          dispatch(changeOptions(options));
        }} 
        placeholder=""
        />
      </Form.Item>

      <Form.Item label="">
        {
          copyText.map((item, index) => {
            return (
              <Input
                onChange={(e) => {
                  copyText[index] = e.target.value;
                  options.inputValue = [...copyText];
                  dispatch(changeOptions(options));
                }}
                placeholder=""
                key={index}
              />

            )
          })
        }
        <Button onClick={addInput}></Button>
      </Form.Item>

      <Form.Item>
        <Checkbox onChange={(e) => {
          options.autoFocus = e.target.checked
          dispatch(changeOptions(options))
        }}></Checkbox>
      </Form.Item>

      <Form.Item>
        <Checkbox onChange={(e) => {
          options.disabled = e.target.checked
          dispatch(changeOptions(options))
        }}></Checkbox>
      </Form.Item>

    </Form>
  );
};

export default App;