import React from 'react'
import styles from './index.module.scss'
import {Button} from 'antd'
import {useDispatch,useSelector} from 'react-redux'
import {clearDomList,deleteDom} from '../../../slice'

export default function App() {
    const dispatch = useDispatch()
    const domId = useSelector((state) => state.home.domId)


    return (
        <div className={styles.display}>
            {/* <Button type='primary' shape='round' onClick={() => alert('')}></Button> */}
            <label></label>
            <Button type='primary' shape='round' onClick={() => dispatch(clearDomList())}></Button>
            <Button type='primary' shape='round' onClick={() => {
                if(!domId) alert('')
                dispatch(deleteDom(domId))
            }}></Button>
            <Button type='danger' shape='round' onClick={() => alert('')}></Button>
        </div>
    )
}